/**
 * Frequently used utility classes.
 */
/* Hides shit */
.hidden {
  text-indent: -9999px;
  visibility: hidden;
  display: none;
}
/* Creates a responsive wrapper that makes our content scale nicely */
.inner {
  position: relative;
  width: 80%;
  max-width: 700px;
  margin: 0 auto;
}
/* Centres vertically yo. (IE8+) */
.vertical {
  display: table-cell;
  vertical-align: middle;
}
.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}
.clearfix:after {
  clear: both;
}
/* Absolute Centering */
/**
 * Page header styles.
 */
/* Big cover image on the home page */
.site-head {
  position: relative;
  display: table;
  width: 100%;
  height: 60%;
  margin-bottom: 5rem;
  text-align: center;
  color: #fff;
  background: #303538 no-repeat center center;
  background-size: cover;
  background: linear-gradient(130deg, #aea5fc 10%, #ff9899 90%);
  background-repeat: repeat;
}
.blog-logo {
  text-decoration: none;
}
/* Yo-logo. Yolo-go. Upload one in ghost/settings/ */
.blog-logo img {
  display: block;
  max-height: 100px;
  width: auto;
  margin: 0 auto;
  line-height: 0;
}
/* The details of your blog. Defined in ghost/settings/ */
.blog-title {
  margin: 10px 0 10px 0;
  font-size: 5rem;
  letter-spacing: -1px;
  font-weight: lighter;
  font-family: 'Lato', sans-serif;
  text-shadow: 0 1px 6px rgba(0, 0, 0, 0.1);
}
.blog-description {
  margin: 0;
  font-size: 1.8rem;
  line-height: 1.5em;
  font-weight: 300;
  font-family: 'Roboto Slab', serif;
  letter-spacing: 0;
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.15);
}
/**
 * Post styles.
 */
/* Every post, on every page, gets this style on its <article> tag */
.post {
  position: relative;
  width: 80%;
  max-width: 700px;
  margin: 4rem auto;
  padding-bottom: 4rem;
  border-bottom: #EBF2F6 1px solid;
  word-break: break-word;
  hyphens: auto;
}
/* Add a little circle in the middle of the border-bottom on our .post
  just for the lolz and stylepoints. */
.post:after {
  display: block;
  content: "";
  width: 7px;
  height: 7px;
  border: #E7EEF2 1px solid;
  position: absolute;
  bottom: -5px;
  left: 50%;
  margin-left: -5px;
  background: #fff;
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  border-radius: 100%;
  box-shadow: #fff 0 0 0 5px;
}
.post-title {
  margin: 0;
}
.post-title a {
  text-decoration: none;
}
.post-excerpt p {
  margin: 1.6rem 0 0 0;
  font-size: 0.9em;
  line-height: 1.6em;
  text-align: justify;
  text-align-last: left;
}
.post-meta {
  display: inline-block;
  margin: 0 0 5px 0;
  font-family: 'Lato', sans-serif;
  font-size: 1.5rem;
  color: #9EABB3;
}
.post-meta a {
  color: #9EABB3;
  text-decoration: none;
}
.post-meta a:hover {
  text-decoration: underline;
}
.user-meta {
  position: relative;
  padding: 0.3rem 40px 0 100px;
  min-height: 77px;
}
.user-image {
  position: absolute;
  top: 0;
  left: 0;
}
.user-name {
  display: block;
  font-weight: bold;
}
.user-bio {
  display: block;
  max-width: 440px;
  font-size: 1.4rem;
  line-height: 1.5em;
}
.publish-meta {
  position: absolute;
  top: 0;
  right: 0;
  padding: 4.3rem 0 4rem 0;
  text-align: right;
}
.publish-heading {
  display: block;
  font-weight: bold;
}
.publish-date {
  display: block;
  font-size: 1.4rem;
  line-height: 1.5em;
}
.comments-area {
  width: 80%;
  max-width: 700px;
  margin: 4rem auto;
}
#comment h1 a {
  text-decoration: none;
}
/* Insert some mad padding up in the header for better spacing */
.post-template .post-header {
  padding: 60px 0;
  text-align: center;
}
.post-template .site-head {
  color: #303538;
  border-bottom: #ebf2f6 1px solid;
  background: none !important;
}
.post-template .site-head:after {
  position: absolute;
  bottom: -5px;
  left: 50%;
  display: block;
  width: 7px;
  height: 7px;
  margin-left: -5px;
  content: '';
  border: #e7eef2 1px solid;
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  border-radius: 100%;
  background: #fff;
  box-shadow: #fff 0 0 0 5px;
}
/* Keep large images within the bounds of the post-width */
.post-content img {
  display: block;
  max-width: 100%;
  margin: 0 auto;
  height: auto;
}
/* Justify post content */
.post-content p {
  text-align: justify;
  text-align-last: left;
}
/* The author credit area after the post */
.post-footer {
  position: relative;
  margin: 4rem 0 0 0;
  padding: 4rem 0 0 0;
  border-top: #EBF2F6 1px solid;
}
.post-footer h4 {
  font-size: 1.8rem;
  margin: 0;
}
.post-footer p {
  margin: 1rem 0;
  font-size: 1.4rem;
  line-height: 1.6em;
}
/* Create some space to the right for the share links */
.post-footer .author {
  margin-right: 180px;
}
/* Drop the share links in the space to the right.
   Doing it like this means it's easier for the author bio
   to be flexible at smaller screen sizes while the share
   links remain at a fixed width the whole time */
.post-footer .share {
  position: absolute;
  top: 4rem;
  right: 0;
  width: 140px;
}
.post-footer .share a {
  font-size: 1.8rem;
  display: inline-block;
  margin: 1.4rem 1.6rem 1.6rem 0;
  color: #BBC7CC;
  text-decoration: none;
}
.post-footer .share a:hover {
  color: #50585D;
}
/* ==========================================================================
   6. Third Party Elements - Embeds from other services
   ========================================================================== */
/* Hexo: Youtube, other video container */
.video-container {
  position: relative;
  padding-top: 56.25%;
  height: 0;
  overflow: hidden;
}
.video-container iframe,
.video-container object,
.video-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  margin-top: 0;
}
/* Hexo: Syntax Highlighter */
figure.highlight {
  background: #fff;
  border-radius: 0.3em;
  border: 1px solid #e1e1e1;
  line-height: 1.45em;
  font-size: .9em;
  margin-bottom: 2.1em;
  color: #222;
  overflow: auto;
  white-space: pre;
  word-wrap: normal;
}
figure.highlight figcaption {
  padding: 7px 10px;
  font-size: .8em;
}
figure.highlight table {
  margin: 0;
}
figure.highlight table > tbody > tr > td {
  padding: 0;
  background: #fff !important;
}
figure.highlight table > tbody > tr > td.gutter {
  max-width: 40px;
  text-align: right;
}
figure.highlight pre {
  border: none;
  margin: 0;
}
/* Theme: Solarized - Github
 * More theme here: http://softwaremaniacs.org/media/soft/highlight/test.html
 */
pre .comment,
pre .template_comment,
.diff pre .header,
pre .javadoc {
  color: #998;
  font-style: italic;
}
pre .keyword,
.css .rule pre .keyword,
pre .winutils,
.javascript pre .title,
.nginx pre .title,
pre .subst,
pre .request,
pre .status {
  color: #333;
