}
pre .number,
pre .hexcolor,
.ruby pre .constant {
  color: #099;
}
pre .string,
pre .tag pre .value,
pre .phpdoc,
.tex pre .formula {
  color: #dd1144;
}
pre .title,
pre .id,
.coffeescript pre .params,
.scss pre .preprocessor {
  color: #900;
  font-weight: bold;
}
.javascript pre .title,
.lisp pre .title,
.clojure pre .title,
pre .subst {
  font-weight: normal;
}
pre .class pre .title,
.haskell pre .type,
.vhdl pre .literal,
.tex pre .command {
